.dashboard-container {
  padding: 30px;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

.dashboard-controls {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.dashboard-controls input {
  padding: 8px 12px;
  width: 60%;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 16px;
}

.dashboard-controls button {
  padding: 8px 16px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.dashboard-controls button:hover {
  background-color: #45a049;
}

.college-table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.college-table th,
.college-table td {
  padding: 12px 15px;
  border: 1px solid #ddd;
  text-align: left;
}

.college-table th {
  background-color: #f4f4f4;
  cursor: pointer;
}

.college-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.action-buttons button {
  margin-right: 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.action-buttons button:hover {
  opacity: 0.8;
}

.delete-btn {
  background-color: #f44336;
  color: white;
}

.delete-btn:hover {
  background-color: #d32f2f;
}

/* Pagination */
.pagination {
  margin-top: 15px;
  text-align: center;
}

.pagination button {
  margin: 0 3px;
  padding: 5px 10px;
  cursor: pointer;
}

.pagination button.active {
  background-color: #4caf50;
  color: white;
  border-radius: 3px;
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: white;
  padding: 20px;
  width: 400px;
  border-radius: 6px;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
}

.modal-content h2 {
  margin-top: 0;
  margin-bottom: 15px;
  text-align: center;
}

.modal-content input {
  width: 100%;
  padding: 8px 10px;
  margin-bottom: 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
}

.modal-buttons button {
  margin-left: 10px;
  padding: 6px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.modal-buttons button:first-child {
  background-color: #4caf50;
  color: white;
}

.modal-buttons button:last-child {
  background-color: #f44336;
  color: white;
}

/* Toast Notification */
.toast {
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #333;
  color: white;
  padding: 10px 15px;
  border-radius: 4px;
  display: flex;
  align-items: center;
}

.toast button {
  margin-left: 10px;
  background: transparent;
  border: none;
  color: white;
  cursor: pointer;
}
